body {
    background-color: #161a33;
    background-image: url("/titleScreen.png");
    background-size:cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin:0;
    scrollbar-width: none !important;
    overflow:scroll;
}
* {
    scrollbar-width: none !important;
}
#settingsButton {
    width: 5vh;
    height:5vh;
}
#archerMajorContainer div::-webkit-scrollbar {
    display: none;
}
#container {
    height: 450vh;

}
.mapTile {
    scrollbar-width: none !important;
    display:flex;
    flex-direction: column;
    position:absolute;
    width:15vw;
    height:20vh;
    margin:10px;
    text-align: center;
    
    border:#6e3433 solid 5px;
    border-radius: 3px;
    background-color: #34aeda;
}
@media (orientation: portrait) {
    .mapTile {
        font-size: 1vh;
    }
}
@media (orientation: landscape) {
    .mapTile {
        font-size: 1vw;
    }
}
  
#archerMajorContainer, #container, #archerMajorContainer > div, #archerMajorContainer > svg{
    height:450vh !important;
}
path:hover {
    stroke: red;
}
path, svg{
    /*
    pointer-events:all;
    */
}
.mapTile img {
    width: 15vw;
    height:13vh;
}
.mapName {
    height:30%;
    display:flex;
    align-items: center;
    justify-content: center;
}
.mapName div {
    padding:0.5vw;
}
.mapDetails {
    background-color: #34aeda;
    height:20%;
    display:flex;
    justify-content: center;
    align-items: center;
}
.mapDetails div {
    display:flex;
    justify-content: center;
    align-items: center;
}
.mapDetails img {
    width:24px;
    height:24px;
    
}







section {
    display:flex;
}
section input {
    display:none;
}
button {
    background: none;
    border: none;
    color: white;
}
button img{
    width:100%;
    height:100%;
}
/* NIE DZIALA W CHUJ OGOLEM */
#settingsButton {
    position:fixed;
    left: 95vw;
    top: 21vh;
    z-index: 3;
    transition: all 0.2s;
}


#menu {
    display:flex;
    flex-wrap: wrap;
    align-items:center;
    justify-content: space-around;
    position: fixed;
    width:96vw;
    
    overflow: hidden;
    background-color:#161a33dd;
    z-index: 2;
    margin-top: 2vh;
    margin-left:2vw;

    /* From https://css.glass */
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);

    transition: all 0.2s;
}
.settingsMenuOpen {
    height: 20vh;
    outline: #7c362e solid 2px;
    border: #541e12 solid 2px;
}
.settingsMenuClosed {
    height: 0;
    border:none;
    outline: none;
}
.settingsButtonClosed {
    top: 2vh !important;
}


label {
    display:flex;
    justify-content:center;
    align-items:center;

    width: 5vh;
    height:5vh;
    color: white;
    background-color: #262a43;
}
label, section img {
    outline: #7c362e solid 2px;
    border: #541e12 solid 2px;
}
section img {
    height:5vh;
}
section {
    display:flex;
    height: 33%;
}

input[type="radio"]:checked+label, input[type="checkbox"]:checked+label, input[type="checkbox"]:checked+label img {
    background-color: #541e12;
}
